/* general */
.nav-weather{
	padding:5px 5px;
	font-family: "Lato", sans-serif;
	color: white;
	width:100%;
	margin-top: 6%;
	-webkit-transition: .3s ease-in-out;
	-o-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
	font-size: 18px;
}

.nav-weather a{
	text-decoration: none;
	list-style-type: none;
	color: white;
	-webkit-transition: .3s ease-in-out;
	-o-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
}

.nav-weather a:hover{
	color:#76966B;
	-webkit-transition: .3s ease-in-out;
	-o-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
}

.nav-weather i{
	margin-left:5px;
	margin-right: 5px;
	font-size: 15px;
}


